      <br><br><br>
      <h3>Info COVID-19 indonesia</h3><br>
      <div class="flex-container">
      <div class="indonesia">
      <small>
        <h3 v-text="indon.name">
        </h3>
        <p>Positif : </p><p v-text="indon.positif"></p>
        <p>Sembuh : </p><p v-text="indon.sembuh"></p>
