      <h3>Info COVID-19 Perpovinsi Di indonesia</h3><br>
    </div>
    <div class="tabelcor">
        <table>
          <thead>
            <tr>
            <th>Provinsi</th>
            <th>Kasus Positif</th>
            <th>Kasus Sembuh</th>
            <th>Kasus Meninggal</th>
            </tr>
          </thead>
          <tbody>
          <tr v-for="prov in provinsi" v-bind:key="prov.FID">
            <td>{{prov.attributes.Provinsi}}</td>
            <td>{{prov.attributes.Kasus_Posi}}</td>
            <td>{{prov.attributes.Kasus_Semb}}</td>
            <td>{{prov.attributes.Kasus_Meni}}</td>
          </tr>
