<script>
import axios from 'axios';

export default {
  name: 'HelloWorld',
  props: {
    head: String
  },
  data(){
    return {
      positif:[],
      sembuh:[],
      meninggal : [],
      indon:[],
      provinsi:[],
      global:[],
      err : '',
      url : "api/corona"
    }
  },
  methods : {
    getTotalPositif : function(){
      axios.get(this.url+"/positif").then((res)=>{
        this.positif = res.data;
        }).catch((err)=>{
            return err;
        });
    },
    getTotalSembuh(){
         axios.get(this.url+"/sembuh").then((res)=>{
            this.sembuh = res.data;
        }).catch((err)=>{
            return err;
        });
    },
    getTotalMeninggal(){
         axios.get(this.url+"/meninggal").then((res)=>{
            this.meninggal = res.data;
        }).catch((err)=>{
            return err;
        });
    },
    getIndonesia(){
      axios.get(this.url+"/indonesia").then((res)=>{
        this.indon = res.data;
      }).catch((err)=>{
        return err;
      });
    },
    getProvinsi(){
      axios.get(this.url+"/indonesia/provinsi").then((res)=>{
        this.provinsi = res.data;
      }).catch((err)=>{
        return err;
      });
    },
    getGlobal(){
      axios.get(this.url+"/global").then((res)=>{
        this.global = res.data;
      }).catch((err)=>{
        return err;
      });
    }
  },
  mounted(){
    this.getTotalPositif(),
    this.getTotalMeninggal(),
    this.getTotalSembuh(),
    this.getIndonesia(),
    this.getProvinsi(),
    this.getGlobal()
  }
}
</script>

